@media print {
  @page {
    size: A4;
  }
  body {
    font-size: 20pt;
  }

  img.avatar {
    filter: grayscale(1);
    -webkit-filter: greyscale(1);
  }
  .main-wrapper .time {
    float: right;
    color: black !important;
    -webkit-print-color-adjust: exact;
  }
  .sidebar-wrapper {
    position: static;
    float: right;
    width: 40%;
    -webkit-print-color-adjust: exact;
  }

  .remove-container.container-block {
    display: none;
  }

  footer {
    display: none;
  }

  .skillset .level-bar-inner {
    background-color: black !important;
    -webkit-print-color-adjust: exact;
  }
}
